<template>
  <div class="home" style="">
    <form @submit.prevent="onFormSubmit()" >
      <div class="sign" style="height:85vh;  display: grid; place-items: center;">
        <div  style="width:100%;">
          <el-row>
            <el-col :span="6"><div class="grid-content"></div></el-col>
            <el-col :span="5" v-if="sign === true">
              <div class="grid-content">
                <h1>Sign in</h1>
              </div>
            </el-col>
            <el-col :span="5" v-if="sign === false">
              <div class="grid-content">
                <h1>Sign up</h1>
              </div>
            </el-col>
            <el-col :span="2"><div class="grid-content"></div></el-col>
            <el-col :span="5" v-if="sign === true">
              <div class="grid-content" style="display: grid;">
                <el-input  placeholder="Email Adress" v-model="INusername"/>
                <el-input placeholder="Password" v-model="INpassword" show-password/>  
                <el-button type="primary" @click="signIn" style="padding: 12px 16px;">login</el-button>
              </div>
            </el-col>
            <el-col :span="5"  v-if="sign === false">
              <div class="grid-content" style="display: grid;">
                <el-input  placeholder="Email Adress" v-model="UPusername"/>
                <el-input placeholder="Password" v-model="UPpassword"/>
                <el-select v-model="gender" placeholder="Gender">
                  <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"/>
                </el-select>
                <el-row>
                  <el-col :span="6"><div class="grid-content">Age:</div></el-col>
                  <el-col :span="18"><div class="grid-content">
                    <el-input-number v-model="age" controls-position="right" :min="0" :max="100"/>
                  </div></el-col>
                </el-row>
                <el-button type="primary" @click="signUp" style="padding: 12px 8px;">register</el-button>
              </div>
            </el-col>
            <el-col :span="6"><div class="grid-content"></div></el-col>
          </el-row>
          <el-row>
            <el-col :span="24"><div class="grid-content">
              <p style="font-size: small; text-align: right;">
                This site is protected by reCAPTCHA and the Google
                <a href="https://policies.google.com/privacy">Privacy Policy</a> and
                <a href="https://policies.google.com/terms">Terms of Service</a> apply.
              </p>
            </div></el-col>
          </el-row>
          <el-button type="success" round v-if="sign === true" @click="signDiv">sign up</el-button>
          <el-button type="success" round v-if="sign === false" @click="signDiv">sign in</el-button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import firebase from 'firebase'
import axios from "axios"

export default {
  name: 'HelloWorld',
  data:()=>{
    return {
      sign: true,
      UPusername: '',
      UPpassword: '',
      INusername: '',
      INpassword: '',
      message: '',
      userPageNum:'',
      logoutText:'',
      options: [{
        value: 'm',
        label: '男性(male)'
      }, {
        value: 'f',
        label: '女性(female)'
      }],
      gender: '',
      age: '',
       company:""
    }
  },
  methods: {
    signDiv: function(){
      this.sign = !this.sign
    },
    signUp: function () {
      firebase.auth().createUserWithEmailAndPassword(this.UPusername, this.UPpassword)
      .then(user => {
        const database = firebase.database();
        const userData = "userData";
        database.ref(userData).push({
          email: this.UPusername,
          gender: this.gender,
          age: this.age
        });
        alert('Create account: ', user.email)
        this.INusername = this.UPusername
        this.INpassword = this.UPpassword
        this.signIn()
      })
      .catch(error => {
        alert(error.message)
      })
    },
    signIn: function () {
      firebase.auth().signInWithEmailAndPassword(this.INusername, this.INpassword).then(
        user => {
          this.$_setuseremail(this.INusername)
          this.setIdRequest(this.INusername)
          this.emailRequest(this.INusername)
          this.INusername = ''
          this.INpassword = ''
          this.UPusername = ''
          this.UPpassword = ''
          this.gender = ''
          this.age = ''
          this.$router.push('/quest').catch(()=>{});
          console.log(user)
        },
        err => {
          alert(err.message)
        }
      )
    },
    async onFormSubmit() {
      // Wait until recaptcha has been loaded.
      await this.$recaptchaLoaded();
 
      // Execute reCAPTCHA with action "login".
      const token = await this.$recaptcha('login');
 
      // Prepare form data
      let formData = new FormData(this.$refs.contactform);
 
      // Appended token in formData
      formData.append('recaptcha-token', token);
 
      // Make an ajax request to your Laravel endpoint.
      axios.post('/your-form-endpoint', formData).then(
        (response) => {
          // handle response
          alert(response.message)
        },
        (error) => {
          // handle errors
          alert(error.message)
        }
      )
    },
    emailRequest: async function(username){
      const params = new URLSearchParams();
      params.append("name",username);
      const postEmail = await axios.post("http://localhost:8040/postEmail", params)
      //const postEmail = await axios.post("https://asia-east1-twitch-MCI.cloudfunctions.net/postEmail", params)
      this.userPageNum = postEmail.data
      if (postEmail.data == "空きがありませんでした"){
        this.$router.push('/stay').catch(()=>{});
      }else{
        this.$router.push('/quest').catch(()=>{});
      }
    },
    setIdRequest: async function(username){
      const params = new URLSearchParams();
      params.append("name",username);
      const IDs = await axios.post("http://localhost:8040/setIDs", params)
      //const postEmail = await axios.post("https://asia-east1-twitch-MCI.cloudfunctions.net/setIDs", params)
      this.company = IDs.data
      console.log(IDs.data)
      this.$_setchannel(IDs.data.channels)
      this.$_setusername(IDs.data.username)
      this.$_setpassword(IDs.data.password)
    }
  }
}
</script>

<style>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #f56691;
}


.el-row{
  display: flex;
  justify-content: center;
  align-items: center;
}

.el-main {
  background-color: #E9EEF3;
  color: #333;
}

input {
  margin: 5px 0;
  padding: 5px;
}

.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
</style>
